import React, { useEffect } from 'react';
import { differenceInDays } from 'date-fns';
import classNames from 'classnames';
import socket from './../../api/ws';
import styles from './TasksList.module.scss';

function TasksListItem ({ task, tasks, setTasks, userName }) {
  const { isDone, body, deadline, authorName, implementerName } = task;

  useEffect(() => {
    function onUpdate (payload) {
      setTasks(tasks => {
        const newTasks = [...tasks];
        newTasks[newTasks.findIndex(t => t.id === payload.id)] = { ...payload };
        return newTasks;
      });
    }

    socket.on('updated', onUpdate);
    return () => {
      socket.off('updated', onUpdate);
    };
  }, []);

  function handleChange ({ target: { checked } }) {
    if (checked)
      socket.emit('task:update', {
        ...task,
        isDone: true,
        implementerName: userName,
      });
  }

  const immediatlyClassName = classNames({
    [styles.immediatly]:
      !isDone &&
      differenceInDays(new Date(deadline), new Date()) <= 3 &&
      differenceInDays(new Date(deadline), new Date()) > 0,
  });

  const thanksgivingClassName = classNames({ [styles.unvisible]: !isDone });

  return (
    <li>
      <div>
        <input type='checkbox' checked={isDone} onChange={handleChange} />
        <span> {body}</span>
      </div>
      <div>
        <span>{authorName}</span>
        <span className={immediatlyClassName}>{deadline}</span>
      </div>
      <div className={thanksgivingClassName}>
        Good job! Thank you, <span>{implementerName}</span>!
      </div>
    </li>
  );
}

export default TasksListItem;
